import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import './Checkout.css';

import {Container, Row, Col} from 'react-bootstrap';

let i = 0;

// component defined here
const Checkout = (props) => {
    let totalPrice = 0;
    if(props.cart.length>0){
        return (
            <Container className="checkout mt-5">
                
                <Row><Col className="heading"><h1>Checkout</h1></Col></Row>
            {
                props.cart.map(product => {
                    return (
                        totalPrice += product.price,
                        <Row key={++i}>
                            <Col className="my-3 eachProduct">
                                <p>Product Title: {product.product_title} <br/>Price: Rs {product.price}</p>
                            </Col>
                        </Row>
                    );
                })
            }
            <Row><Col className="total-price"><h3>Total Price: Rs {totalPrice}</h3></Col></Row>

            <Row className="my-5"><Col><Link to="/ecommerce"><button className="buyButton" type="button" onClick={() => alert("Order placed!!!")}>Buy</button></Link></Col></Row>

        </Container>
        );
    }
    return <Container><Row><Col className="text-center text-danger my-5 font-weight-bold">Nothing bought yet. GO SHOPPING!!!</Col></Row></Container>;

};

const mapStateToProps = (state) => {
    
    console.log();
    return {
        cart: state.cart || JSON.parse(localStorage.getItem("cart")),
    }
}
export default connect(mapStateToProps)(Checkout);