import React from 'react';
import './ShowCart.css';

import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {removeFromCart} from '../../actions';

import {Container, Row, Col} from 'react-bootstrap';

let i = 0;

// component defined here
const ShowCart = (props) => {
    if(props.cart.length>0){
        return (
        <Container className="showCart mt-5">
            
            <Row><Col><h1>Cart</h1></Col></Row>
            {
                props.cart.map(product => {
                    return (
                        <Row key={++i} className="my-3">
                            <Col>
                                <span>{product.product_title}    Rs{product.price}</span>
                                <button className="remove-button" onClick={() => props.removeFromCart(product)}>Remove</button>
                            </Col>
                        </Row>
                    );
                })
            }
            
            <Link to="/ecommerce/showCart/checkout/">
                <button className="checkout-button my-5" type="button">
                    Checkout
                </button>
            </Link>
        
        </Container>
        );
    }
    return <Container><Row><Col className="text-center my-5"><h3 className="text-danger">Cart is empty. GO SHOPPING!!!</h3></Col></Row></Container>;

};

const mapStateToProps = (state) => {
    
    return {
        cart: state.cart || JSON.parse(localStorage.getItem("cart")),
    }
}
export default connect(mapStateToProps, {removeFromCart})(ShowCart);